:root {
  --purple-dark: #5b2a86;
  --purple-mid: #9c7dff;
  --purple-soft: #efe7ff;
  --bg: #f6f2ff;
  --text: #221a29;
}

body {
  background-color: var(--bg);
  color: var(--text);
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  background-color: var(--purple-dark);
  color: #fff;
  text-align: center;
  opacity: .97;
  padding: 24px;
  box-shadow: 0 3px 6px #0003;
}

nav a {
  color: #fff;
  opacity: .9;
  margin: 0 12px;
  font-weight: bold;
  text-decoration: none;
}

nav a:hover {
  opacity: 1;
  text-decoration: underline;
}

section {
  background-color: var(--purple-soft);
  border: 1px solid var(--purple-mid);
  border-radius: 10px;
  margin-bottom: 24px;
  padding: 20px;
  box-shadow: 0 4px 10px #00000014;
}

h2 {
  color: var(--purple-dark);
  border-bottom: 2px solid var(--purple-mid);
  padding-bottom: 6px;
}

.project {
  border: 1px solid var(--purple-mid);
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 15px;
  transition: transform .2s, box-shadow .2s;
  box-shadow: 0 3px 8px #00000014;
}

.project:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px #0000001f;
}

.project img {
  border: 2px solid var(--purple-mid);
  border-radius: 6px;
  margin-top: 10px;
  display: block;
}

form {
  border: 1px solid var(--purple-mid);
  background-color: #fff;
  border-radius: 8px;
  padding: 16px;
  box-shadow: 0 4px 8px #00000014;
}

label {
  color: var(--purple-dark);
  font-weight: bold;
}

input, textarea {
  border: 1px solid var(--purple-mid);
  border-radius: 6px;
  outline: none;
  width: 100%;
  margin-top: 6px;
  padding: 10px;
}

input:focus, textarea:focus {
  box-shadow: 0 0 0 .2rem #9c7dff40;
}

input[type="submit"], .btn-outline-primary {
  background-color: var(--purple-dark);
  color: #fff;
  border: 1px solid var(--purple-dark);
  border-radius: 6px;
}

input[type="submit"]:hover, .btn-outline-primary:hover {
  color: #fff;
  background-color: #49206a;
  border-color: #49206a;
}

footer {
  background-color: var(--purple-dark);
  color: #fff;
  text-align: center;
  opacity: .97;
  padding: 20px;
  box-shadow: 0 -3px 6px #0003;
}
